<template lang="html">
  <div class="script__unit-list">
    <h5>Certification Plan {{this.certCurrent}}</h5>
    <ul>
      <li
        v-for="(data, i) in listScripts"
        :key="i"
        @click="certPlanTab(i, data.unit)"
        :class="{'list--active': i == activeIndex}"
      >{{data.unit}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  components: {

  },
  props: [
    'certTab',
    'certCurrent'
  ],
  data () {
    return {
      unitCert: null,
      activeIndex: null
    }
  },
  computed: {
    listScripts () {
      const whichCert = this.certTab
      const data = this.$store.state[whichCert].coachSchedules.data.data
      const filteredData = [];
      data.filter((item) =>{
        var i = filteredData.findIndex(x => (x.unit == item.unit));
        if(i <= -1){
              filteredData.push(item);
        }
        return null;
      });

      return filteredData
    }
  },
  methods: {
    certPlanTab (i, unit) {
      this.activeIndex = i

      const whichCert = this.certTab
      const unitCert = this.$store.state[whichCert].coachSchedules.data.data.filter(x => x.unit === unit)
      // console.log(unitCert);
      this.$store.commit("selectedScriptB2IA1", unitCert)
    }
  }
}
</script>

<style lang="css">
</style>



