<template lang="html">
  <div class="script-title">
    <content-loader
      v-if="!handleLoader"
  		:height="1060"
  		:width="1000"
  		:speed="2"
  		primaryColor="#f3f3f3"
  		secondaryColor="#ecebeb"
  	>
  		<rect x="12.4" y="17.83" rx="0" ry="0" width="72.54" height="21.5" />
  		<rect x="93.88" y="17.83" rx="0" ry="0" width="72.54" height="21.5" />
  		<rect x="173.99" y="17.83" rx="0" ry="0" width="72.54" height="21.5" />
  		<rect x="254.11" y="17.83" rx="0" ry="0" width="72.54" height="21.5" />
  		<rect x="12.4" y="43.83" rx="0" ry="0" width="72.54" height="21.5" />
  		<rect x="407.9" y="19.83" rx="0" ry="0" width="553.15" height="561" />
  		<rect x="13.93" y="84.83" rx="0" ry="0" width="351.38" height="20" />
  		<rect x="13.93" y="108.83" rx="0" ry="0" width="351.38" height="20" />
  		<rect x="13.93" y="133.83" rx="0" ry="0" width="351.38" height="20" />
  		<rect x="12.4" y="158.83" rx="0" ry="0" width="351.38" height="20" />
  		<rect x="12.4" y="183.83" rx="0" ry="0" width="351.38" height="20" />
  		<rect x="12.4" y="207.83" rx="0" ry="0" width="351.38" height="20" />
  		<rect x="12.4" y="233.83" rx="0" ry="0" width="351.38" height="20" />
  		<rect x="12.4" y="259.83" rx="0" ry="0" width="351.38" height="20" />
  		<rect x="12.4" y="284.83" rx="0" ry="0" width="351.38" height="20" />
  	</content-loader>

    <div class="" v-if="handleLoader">
      <h2>Coaching Script</h2>
      <div class="script">
        <div class="script__left">
          <CoachingScript />
        </div>
        <div class="script__right">
          <Scripts />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ContentLoader } from "vue-content-loader"
import CoachingScript from '../../coach-components/coaching-script/CoachingScript';
import Scripts from '../../coach-components/coaching-script/b2i-script/Scripts';

export default {
  components: {
    ContentLoader,
    CoachingScript,
    Scripts
  },
  created () {
    this.$store.dispatch('GET_SCRIPT_B2I_A1')
    this.$store.dispatch('GET_SCRIPT_B2I_A2')
    this.$store.dispatch('GET_SCRIPT_B2I_B1')
    this.$store.dispatch('GET_SCRIPT_B2I_B2')
    this.$store.dispatch('GET_SCRIPT_B2I_C1')
    this.$store.dispatch('GET_SCRIPT_B2I_C2')
  },
  computed: {
    handleLoader () {
      const data = this.$store.state.stateScriptB2IC1
      if (data == null) {
        return false
      } else {
        return true
      }
    }
  }
}
</script>

<style lang="scss">
@import './CoachingScript.scss';
</style>



