<template lang="html">
  <div class="">
    <div class="">
      <h5>Certification Level</h5>
      <div class="script__tab-2">
        <div
          @click="clickedCert('stateScriptB2IA1', 'A1')"
          :class="{'list--active': 'stateScriptB2IA1' == activeIndex}"
          class="script__tab-each"
        >
          A1
        </div>
        <div
          @click="clickedCert('stateScriptB2IA2', 'A2')"
          :class="{'list--active': 'stateScriptB2IA2' == activeIndex}"
          class="script__tab-each"
        >
          A2
        </div>
        <div
          @click="clickedCert('stateScriptB2IB1', 'B1')"
          :class="{'list--active': 'stateScriptB2IB1' == activeIndex}"
          class="script__tab-each"
        >
          B1
        </div>
        <div
          @click="clickedCert('stateScriptB2IB2', 'B2')"
          :class="{'list--active': 'stateScriptB2IB2' == activeIndex}"
          class="script__tab-each"
        >
          B2
        </div>
        <div
          @click="clickedCert('stateScriptB2IC1', 'C1')"
          :class="{'list--active': 'stateScriptB2IC1' == activeIndex}"
          class="script__tab-each"
        >
          C1
        </div>
      </div>
    </div>
    <div class="">
      <ListScript :certTab="certTab" :certCurrent="certCurrent" />
    </div>
  </div>
</template>

<script>
import ListScript from './b2i-script/ListScript';

export default {
  components: {
    ListScript,
  },
  data () {
    return {
      certTab: "stateScriptB2IA1",
      certCurrent: "A1",
      activeIndex: null
    }
  },
  methods: {
    clickedCert (data, cert) {
      this.activeIndex = data
      this.certTab = data
      this.certCurrent = cert
      // console.log(this.certTab);
    }
  }
}
</script>

<style lang="css">
</style>



